import React, { Component } from 'react'
import store from '../../store.js'
import { NavLink } from 'react-router-dom'
import searchFunc from '../../Commponents/Search/searchFunc'
import Search from '../Search/Search.jsx'

export default class Category extends Component {

    state = { data: store }
    
    search = () => {
        let searchResult = searchFunc()
        this.setState({ data: searchResult })
    }

    
    componentDidMount() {
        let pathparts = window.location.pathname.split('/');
        let categoryName = pathparts[pathparts.length - 1].replace("%20", " ")
        let products = store.filter((elm) => elm.category == categoryName);
        this.setState({ data: products })
    }
    
    render() {       
        
        return (
            <section className="products container mt-5 py-5">

                <Search search={this.search} />

                <div className="row align-items-end ">
                    {this.state.data.map((value, index) => {
                        return (
                            <div key={index} className=" col-lg-4 col-md-6 mb-5">
                                <div className="item text-center ">
                                    <div className=" overflow-hidden">
                                        <NavLink to={`/viewProduct/${value.id}`}>
                                            <img className="w-75 product_image" src={value.image} alt="" />
                                        </NavLink>
                                    </div>                                    
                                    <h5 className="mt-4">{value.title}</h5>
                                    <p>{value.price}</p>
                                    <button onClick={()=>(this.props.additems(value,1))}  className="btn btn-lg bg-main w-100 text-white">Add To Cart</button>
                                </div>
                            </div>
                        ) 
                    })}                  
                </div>
            </section>
        )
    }
}
