import React, { Component } from 'react'

export default class GoTopIcon extends Component {

    toTop = () => {
        window.scrollTo(0,0)
    }

    componentDidMount() {
        window.onscroll = () => {
            {
                let elem = document.getElementById('topIcon')
                if (window.scrollY >= (window.innerHeight-100)) {
                    elem.classList.remove('d-none')
                } else {
                    elem.classList.add('d-none')            
                }
            }
        }
    }

    render() {
        // window.onscroll = this.hideIcon
        return (
            <div>
                {/* <div id="vi" className="bg-danger position-absolute">Lorem i</div>
                <div id="vi2" className="bg-info position-absolute">Lorem i</div> */}
                <i id="topIcon" onClick={this.toTop} className=" d-none fas fa-arrow-circle-up "></i>
            </div>
        )
    }
}
