import React, { Component } from 'react'
import store from '../../store.js'
export default class Cart extends Component {
    render() {
        // console.log(this.props)
        return (
            <div className="container mt-5 py-5">
               <table className="table text-center">
                    <thead>
                        <tr>
                            <th scope="col">#</th>
                            <th  className="text-left title"> Name</th>
                            <th scope="col">Price</th>
                            <th scope="col">Image</th>
                            <th scope="col">QTY</th>
                            <th scope="col">Remove</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.props.products.map((value,index)=> {
                            return (
                                <tr key={index} className="w-100">
                                    <th scope="row">{value.id}</th>
                                    <td className="text-left  title">{value.title}</td>
                                    <td>{value.price}</td>
                                    <td><img src={value.image} alt="" /></td>
                                    <td>
                                        <button onClick={() => (this.props.changeQt(value,-1))} className="btn btn-info mx-3">-</button>
                                        {value.qty}
                                        <button onClick={() => (this.props.changeQt(value,1))} className="btn btn-info mx-3">+</button>                              
                                    </td>
                                    
                                    <td>
                                        <button onClick={() => (this.props.deletitem(value))} className="btn btn-lg">
                                            <i className="fas fa-trash text-danger fa-fw mr-2"></i>
                                        </button>
                                    </td>
                                </tr>
                            )
                        })}
                       
                    </tbody>
                </table>
            </div>
        )
    }
}
