import React, { Component } from 'react'
import store from '../../store'
import { NavLink } from 'react-router-dom'

export default class ViewProduct extends Component {
    render() {
        let pathid = window.location.pathname.split('/');
        let index = store.findIndex((elm) => elm.id === Number(pathid[pathid.length-1]))
        // console.log(index);
        return (
            <div className="container my-5 py-5">
                <div className="row justify-content-between align-items-center py-5">
                    <div className="col-lg-4 col-md-5">
                        <div className="item">
                            <img className="w-100" src={store[index].image} alt="" />
                        </div>
                    </div>
                    <div className="col-lg-7 col-md-6 my-3">
                        <div className="item">
                            <h2 className="text-danger mb-3">{store[index].price}$</h2>
                            <h3 className="mb-5">{store[index].title}</h3>
                            <h5>Description:</h5>
                            <p>{store[index].description}</p>
                            <NavLink to="/">store</NavLink>
                            <span> , </span>
                            <NavLink to={`/category/${store[index].category}`}> {store[index].category} </NavLink>
                            <br></br>
                            <button onClick={()=>(this.props.additems(store[index],1))} className="btn btn-lg bg-main w-75 text-white mt-5">Add To Cart</button>

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
