import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

export default class Navbar extends Component {
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-sm navbar-dark bg-main fixed-top ">
                    <div className="container pt-2">
                        <NavLink className="navbar-brand" to="/home"><h3>Store</h3></NavLink>
                        
                        <div className="px-2" id="navbarSupportedContent ">
                            <ul className="navbar-nav ml-auto">

                                <li className="nav-item text-white mt-2 dropdown ">

                                    <div className="btn-group" role="group" aria-label="Button group with nested dropdown">
                                        <div className="btn-group" role="group">
                                            <button id="btnGroupDrop1" type="button" className="btn text-white dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                Category
                                            </button>
                                            <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
                                                <NavLink className="dropdown-item" to="/category/men's clothing">men's clothing</NavLink>
                                                <NavLink className="dropdown-item" to="/category/women's clothing">women's clothing</NavLink>
                                                <NavLink className="dropdown-item" to="/category/jewelery">jewelery</NavLink>
                                                <NavLink className="dropdown-item" to="/category/electronics">electronics</NavLink>
                                            </div>
                                        </div>
                                    </div>

                                    <span className="ml-3" >{this.props.totalPrice.toFixed(2)}$</span>

                                    <NavLink className="cart-link position-relative" to="/cart"><span><i className="fas fa-shopping-cart fa-fw"></i></span>                               
                                        <span className="badge badge-info p-2 position-absolute ">{this.props.totalQt}</span>
                                    </NavLink>                                
                                </li>
                            </ul>
                        </div>
                    </div>
                </nav>
            </div>
        )
    }
}
