

// state={data:store}
import store from '../../store'

function search() {
    let pathparts = window.location.pathname.split('/');
    let pageName = pathparts[pathparts.length - 1].replace("%20", " ")

    let data = store

    if (pageName != "home") {
        data = store.filter((elm) => elm.category == pageName);
    }
        
    let searchInput = document.getElementById("searchInput")
    let text = searchInput.value;

    let searchResult = data.filter((elm) => (elm.title.toLowerCase().includes(text.toLowerCase()) || elm.category.toLowerCase().includes(text.toLowerCase())));

    return searchResult
}
    

export default search