import { Component } from "react";

// _________________ import Commponents _________________________________
import Navbar from "./Commponents/Navbar/Navbar";
import Home from "./Commponents/Home/Home";
import Cart from "./Commponents/CartView/Cart";
import ViewProduct from "./Commponents/ViewProduct/ViewProduct";
// _________________ import _________________________________

import {Route , Switch} from 'react-router-dom'
import Category from "./Commponents/Category/Category";
import GoTopIcon from "./Commponents/goTopIcon/GoTopIcon";


class App extends Component{

  state = {items: []}
  
  addToLocalStorage(data) {
    localStorage.setItem("data",JSON.stringify(data))
  }

  additems = (item,qt) => {   
    let cart = this.state.items
    let index = cart.findIndex((elm) => elm.id === item.id)
    
    if (index != -1) {
      if (!(cart[index].qty == 1 && qt == -1)) {
        cart[index].qty += qt;
       
      }                
    } else {
      cart.push({ ...item, qty: 1 })
    }

    this.setState({items: cart})
    this.addToLocalStorage(cart)
  }
      

  deletitem = (item) => {
    let cart = this.state.items.filter((elm) => elm.id !== item.id);
    this.setState({ items: cart })
    this.addToLocalStorage(cart)
    // console.log("delete item")
  }

  componentDidMount() {
    let data = JSON.parse(localStorage.getItem("data"))
    if (data != null) {
      this.setState({ items: data })
    }
  }
  
  render() {
    let totalQt = this.state.items.reduce((x,y) => x+y.qty , 0)
    let totalPrice = this.state.items.reduce((x,y) => x+(y.price*y.qty) , 0)
    return (
      
      <>        
        <Navbar totalQt={totalQt} totalPrice={totalPrice} />
        <Switch>
          <Route path='/home' component={()=> <Home  additems={this.additems} />} />
          <Route path='/cart' component={() => <Cart  products={this.state.items} deletitem={this.deletitem} changeQt={this.additems} />} />
          <Route path='/viewProduct' component={() => <ViewProduct additems={this.additems}  />} />
          <Route path='/category' component={() => <Category additems={this.additems}   />} />
          <Route path='/' component={()=> <Home additems={this.additems} />} />
        </Switch>
        <GoTopIcon />
      </>
    )
  }
}

export default App;
